export class Site {
    constructor(selector){
        this.$el = document.querySelector(selector)

    }

    render(model){
        this.$el.innerHTML =""// –æ—á–∏—Å—Ç–∫–∞ —à–∞–±–ª–æ–Ω–∞
        model.forEach(point =>{
   
    
           
                this.$el.insertAdjacentHTML("beforeend",point.toHTML());
            
        })

    }
}